/* General container styling */
.interactive-app {
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
    max-width: 600px;
    margin: 0 auto;
    background-color: var(--bg-color, white);
    transition: background-color 0.3s ease;
  }
  
  /* Styling for buttons */
  .interactive-app button {
    padding: 10px 15px;
    margin: 5px 0;
    font-size: 16px;
    color: #fff;
    background-color: #007bff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .interactive-app button:hover {
    background-color: #0056b3;
  }
  
  /* Styling for file upload */
  .interactive-app input[type="file"] {
    display: block;
    margin-top: 15px;
    font-size: 14px;
  }
  
  /* Styling for images */
  .interactive-app img {
    margin-top: 10px;
    width: 150px;
    height: auto;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  /* Styling for JSON data display */
  .interactive-app pre {
    background-color: #f5f5f5;
    padding: 10px;
    border-radius: 4px;
    border: 1px solid #ddd;
    max-height: 200px;
    overflow-y: auto;
  }
  
  /* Section titles */
  .interactive-app h3 {
    margin-top: 20px;
    font-size: 18px;
    color: #333;
  }
  