.not-found {
    text-align: center;
    padding: 50px;
    color: #333;
  }
  
  .not-found h1 {
    font-size: 72px;
    margin-bottom: 20px;
    color: #ff4d4d;
  }
  
  .not-found p {
    font-size: 18px;
    margin-bottom: 30px;
  }
  
  .home-link {
    color: white;
    background-color: #007bff;
    padding: 10px 20px;
    border-radius: 4px;
    text-decoration: none;
    transition: background-color 0.3s ease;
  }
  
  .home-link:hover {
    background-color: #0056b3;
  }
  